import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, FlatList, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import culina from '../assets/wall-culina.png';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Start extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: [],
      carts: []
    };
  }

  async componentDidMount() {
    const { navigation, route } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
    console.log('start', await Linking.getInitialURL())
    if (!route.params.d || !route.params.o || !route.params.l || !route.params.s) {
      navigation.navigate('Error', {
        d: route.params.d
      });
      return;
    }
    this.doGet();
  }

  async didFocus() {
    // window.history.replaceState(null, '', await Linking.getInitialURL());
  }

  componentWillUnmount() {
    this.sub();
  }

  doGet() {
    const { route } = this.props;

    let serviceEntry;
    let promiseArray = [];
    let url;
    let params;
    let promiseObject;

    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }

    url = serviceEntry + 'api/foldergrps';
    params = new URLSearchParams();
    params.append('orgId', route.params.o);
    params.append('shopId', route.params.s);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'foldergrps' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/folders';
    params = new URLSearchParams();
    params.append('orgId', route.params.o);
    params.append('shopId', route.params.s);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'folders' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/menus';
    params = new URLSearchParams();
    params.append('orgId', route.params.o);
    params.append('shopId', route.params.s);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'menus' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/mods';
    params = new URLSearchParams();
    params.append('orgId', route.params.o);
    params.append('shopId', route.params.s);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'mods' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/mod-bundles';
    params = new URLSearchParams();
    params.append('orgId', route.params.o);
    params.append('shopId', route.params.s);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'modBundles' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/settings';
    params = new URLSearchParams();
    params.append('appCode', 'RESTMENU');
    params.append('orgId', route.params.o);
    params.append('shopId', route.params.s);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'settings' };
    promiseArray.push(promiseObject);

    Promise.all([
      ...promiseArray.map(el =>
        fetch(el.url)
          .then(response => response.json())
          .then(response => {
            console.log('fetching', el.nameString, response);
            return ({ [el.nameString]: response });
          })
      )
    ])
      .then(response => {
        const masters = response.reduce((o, item) => ({ ...o, [Object.keys(item)[0]]: Object.values(item)[0] }), {});
        console.log('masters', masters);
        AsyncStorage.setItem('masters', JSON.stringify(masters));
      })
  }

  render() {
    const { navigation, route, } = this.props;

    return (
      <View style={{ flex: 1, backgroundColor: 'white', justifyContent: 'flex-end' }}>
        <View style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }}>
          <Image
            style={{ flex: 1 }}
            source={{
              uri: route.params.d === 'culina' ? culina : null
            }}
            resizeMode='cover'
          />
        </View>
        {
          route.params.t ?
            <View style={{ position: 'absolute', bottom: 180, width: 60, height: 60, backgroundColor: theme.grey1, alignItems: 'center', justifyContent: 'center' }}>
              <Text style={{ fontFamily: 'demi', fontSize: 10, letterSpacing: 1, color: 'white' }}>TABLE</Text>
              <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 1, color: 'white' }}>{route.params.t}</Text>
            </View>
            :
            <View style={{ position: 'absolute', bottom: 180, width: 60, height: 60, backgroundColor: theme.grey1, alignItems: 'center', justifyContent: 'center' }}>
              <Text style={{ fontFamily: 'demi', fontSize: 10, letterSpacing: 1, color: 'white' }}>TAKE</Text>
              <Text style={{ fontFamily: 'demi', fontSize: 10, letterSpacing: 1, color: 'white' }}>AWAY</Text>
            </View>
        }
        <TouchableOpacity style={{ margin: 10, height: 60, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}
          onPress={() =>
            navigation.navigate('Menu', {
              d: route.params.d,
              l: route.params.l,
              o: route.params.o,
              s: route.params.s,
              t: route.params.t,
            }
            )}>
          <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>START AS GUEST</Text>
        </TouchableOpacity>
        <TouchableOpacity style={{ margin: 10, height: 60, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}
          onPress={() =>
            navigation.navigate('Login', {
              d: route.params.d,
              l: route.params.l,
              o: route.params.o,
              s: route.params.s,
              t: route.params.t,
            }
            )}>
          <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>START AS COMOCLUB</Text>
        </TouchableOpacity>
      </View>
    );
  }

}

const styles = StyleSheet.create({
});
